<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="6">
        <v-autocomplete
          v-model="team"
          :items="teams"
          item-text="fullName"
          item-value="fullName"
          outlined
          dense
          label="Team"
          @change="teamSelected()"
        ></v-autocomplete>
      </v-col>
    </v-row>
    <v-row> 
      <v-expansion-panels>
        <template v-for="(data, index) in datasheets">
          <v-expansion-panel :key="index">
            <v-expansion-panel-header>
              <v-row
                no-gutters
                class="d-flex align-center"
              >
                <v-col cols="2" class="text-right">
                  {{ data.condition === "home" ? data.team : data.adversary }}
                </v-col>
                <v-col cols="1" class="text-center">
                  VS
                </v-col>
                <v-col cols="2" class="text-left">
                  {{ data.condition != "home" ? data.team : data.adversary }}
                </v-col>
                <v-col cols="2" class="text-center">
                  <div>journey: {{ data.journey }}</div>
                </v-col>
                <v-col cols="2" class="text-center">
                  <div>date: {{ data.date }}</div>
                </v-col>
                <v-col cols="3">
                  <div class="d-flex flex-row justify-space-between">
                    <div>Football Field: {{ data.footballField }}</div>
                    <div class="pr-5">
                      <v-icon
                        small
                        color="success"
                        @click="go(data.id)"
                      >
                        mdi-pencil
                      </v-icon>
                    </div>
                  </div>
                </v-col>
              </v-row>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-row>
                <v-col cols="12">
                  <v-data-table
                    dense
                    fixed-header
                    hide-default-footer
                    disable-pagination
                    height="280px"
                    :headers="headers"
                    :items="data.players"
                    item-key="name"
                    class="elevation-1"
                  >
                    <template v-slot:item.substitute="{item}">
                      <div v-if="item.substitute">
                        <v-icon>
                          done
                        </v-icon>
                      </div>
                    </template>
                    <template v-slot:item.replacement="{item}">
                      <div v-if="item.replacement">
                        <v-icon>
                          done
                        </v-icon>
                      </div>
                    </template>
                  </v-data-table>
                </v-col>
              </v-row>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </template>
      </v-expansion-panels>
    </v-row>
  </v-container>
</template>

<script>
import firebase from "@/firebaseConfig";
import listTeams from "../libary/teams";
const db = firebase.firestore();
export default {
  name: 'RecordsHome',
  components: {

  },
  data: () => ({
    datasheets: [],
    team: "",
    teams: listTeams,
    players: [],
    headers: [
      {
        text: 'Jersey',
        align: 'start',
        sortable: false,
        value: 'jersey',
      },
      { text: 'Name', value: 'name' },
      { text: 'Replacement', align: 'center', value: 'replacement' },
      { text: 'Goal scored by', align: 'center', value: 'goalScoredBby' },
      { text: 'Yellow card', align: 'center', value: 'yellowCard' },
      { text: 'Red card', align: 'center', value: 'redCard' },
      { text: 'Substitute', align: 'center', value: 'substitute' },
    ],
  }),
  methods: {
    teamSelected () {
      const vm = this;
      vm.datasheets = []
      vm.players = []
      db.collection("datasheets").where("team", "==", this.team)
        .get().then((querySnapshot) => {
          // let index = 0
          querySnapshot.forEach((doc) => {
            vm.datasheets.push({
              id: doc.id,
              adversary: doc.data().adversary,
              date: doc.data().date,
              footballField: doc.data().footballField,
              journey: doc.data().journey,
              team: doc.data().team,
              players: []
            })
            console.log(vm.datasheets.length);
            doc.data().players.forEach((p,) => {
              vm.datasheets[vm.datasheets.length - 1].players.push(p)
              // vm.datasheets[index].players.push(p)
            })
            // index++
          });
      });
    },
    go (id) {
      this.$router.push({path:'/dashboard/records/add', query:{ id }})
    }
  }
}
</script>